@import '../../variables';

.players-hand {
  &.tut-hand {
    transform: translate(-38%, -79vh) scale(1.2);
    transition: 0.5s ease-in-out;
    .card {
      width: 35vw;
    }
  }
  position: absolute;
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(1px, max-content));
  left: 40%;
  top: $players-hand-position;
  transition: .3s ease-in-out;
  transform: translateX(-50%) rotate(4deg);
  &:hover {
    transform: translate(-50%, -25vh) scale(1.2);
    .card {
      height: 26vh;
      width: 13vw;
      transition: .3s ease-in-out;
      transform: rotate(4deg);
      &:hover {
        transform: scale(1.2) rotate(-7deg) translateY(-9vh);
        z-index: 3;
      }
    }
  }
}
