.landing-container {
  position: relative;
  background: #a6b5ce;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
}

.landing-container:after {
  content: '';
  position: absolute;
  background-image: url('../../assets/landing.png');
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.5;
  z-index: 1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: block;
}

.landing-container .login {
  z-index: 2;
  position: relative;
}

.landing-container-content {
  font-family: 'Cactus';
  height: 100%;
  width: 100%;
  position: absolute;
  display: grid;
  grid-template-columns: 1fr 3fr 4fr 2fr 4fr;
  grid-template-rows: 4fr 1fr 1fr 4fr 1fr 0.5fr;
  
  .admin-login {
    grid-area: 5/4/6/5;
  } 

  .user-login {
    grid-area: 2/2/4/3;
  }
}
