@import '../../mixins';
@import '../../variables';

.tutorial-container {
  @include center;
  height: $board-height;
  width: $board-width;
  z-index: 10;
  @include grid-settings;
  // grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1.5fr 1fr 1fr 1fr 1.7fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  // grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 0.25fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr 1fr;
  grid-template-rows: repeat(6, 1fr) repeat(5, 0.2fr) 1fr 1fr;
  display: grid;
  // grid-template-areas:
  //   '. a .'
  //   '. d .'
  //   '. d .';
}

.greyed-out {
  @include grey-out;
}

// .minion-message-container {
//   display: grid;
// }

.dialog-box {
  font-size: medium;
  background: ivory;
  min-height: 20vh;
  border-radius: 1rem;
  text-align: center;
  justify-self: stretch;
}

.dialog-box-zillian {
  grid-area: 5 / 2 / 10 / 6;
  align-self: end;
}

.dialog-box-climmie {
  grid-area: 2 / 3 / 4 / 8;
  align-self: end;
}

.dialog-box-pointer {
  height: 3rem;
  width: 3rem;
  background: ivory;
  margin: 0 auto;
  transform: rotate(45deg);
  border-radius: 0 0 1.2rem 0;
  z-index: -2;
  grid-area: 8 / 3 / 12 / 4;
}
.dialog-box-pointer-climmie {
  grid-area: 2 / 7 / 3 / 9;
  justify-self: start;
  transform: rotate(135deg);
  align-self: end;
}
.dialog-box-pointer-carbion {
  grid-area: 2 / 2 / 4 / 4;
  transform: rotate(315deg);
  justify-self: end;
  align-self: center;
}

.blurred {
  background: gray;
  filter: blur(2rem);
  position: relative;
  top: -31.5rem;
  /* 	transform: scale(1.05); */
  z-index: -1;
}

.tut-button,
.waiting-for-players {
  grid-area: 11/5/13/6;
  padding: 1rem;
  font-family: 'Cactus';
}

.tut-button {
  font-size: large;
}

.waiting-for-players {
  background-color: #333;
  border: 0px;
  color: $white;
  font-weight: lighter;
}

.waiting-climmie-spot,
.tut-button-core,
.tut-button-disruption {
  grid-area: 4 / 7 / 6 / 8;
}

.minion-message {
  padding: 1vw;
}

.tut-role-card {
  grid-area: 3 / 7 / 12 / 10;
  justify-self: end;
  align-self: end;
}
