@import '../../colors';

.chat-message-container {
  position: relative;
  height: 92%;
  width: 100%;
  overflow-y: scroll;
}

.chat-message-beginning {
  color: rgb(165, 165, 165);
  text-align: center;
  font-style: italic;
  font-size: medium;
  margin-top: 2px;
}

.chat-msg-element {
  color: $white;
  margin-left: 0.5rem;
  background-color: #0b3c5fc2;
  margin: 2.5%;
  padding: 0.2rem;
  border-radius: 5px;
  .msg-meta {
    font-size: x-small;
    font-style: italic;
  }
}