@import '../../colors';

.chat-input-container {
  width: 100%;
  height: 8%;
}

.chat-input-form {
  height: 100%;
  display: flex;
  flex-direction: row;
}

.chat-input-container input {
  height: 100%;
  width: 85%;
  padding: 0px 0px 0px 0.3rem;
  border: 0px;
  background-color: #0b3c5fc9;
  color: $white;
}

.chat-input-container button {
  color: white;
  width: 15%;
  font-size: 1.4rem;
  font-family: 'Cactus';
  background-color: $day-of-adaptation-yellow;
  border: 0px;
  height: 100%;
  padding: 0;
  &:hover {
    cursor: pointer;
    background-color: darken($day-of-adaptation-yellow, 10%);
  }
}
