.player-component {
  padding: 1rem;
  color: white;
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;


  .rooms-player-pic {
    flex: 2;
    height: 100%;
    width: 100%;
    background-image: url('../../assets/roles/man-3.svg');
    background-position: center;
    background-repeat: no-repeat;
    justify-self: center;
    grid-area: 1/1/3/2;
  }

  .room-player-name {
    font-family: 'Cactus';
    font-size: 1.7rem;
    flex: 1;
    justify-self: center;
  }
}