@import '../../mixins';


.discarded-display-grey-out{
  @include grey-out;
  @include top-left;
}

.discarded-display {
  @include top-left;
  display: grid;
  z-index:14;
  grid-template-rows: 0.2fr 1fr 0.2fr 1fr;
  grid-template-areas:
  'init-title'
  'initiative'
  'dis-title'
  'disruption';
  .discarded-text{
    width: 100vw;
    font-size: 1.5rem;
    text-align: center;
    color: white;
    }
  .dis {
    display: grid;
    grid-gap: 1vw;
    padding: 1vw;
    grid-template-columns: repeat(auto-fill, minmax(20vw, 1fr));
    grid-auto-flow: column;
    grid-auto-columns: minmax(20vw, 1fr);
    overflow-x: auto;}
    .init-title {
      grid-area: init-title;
    }
    .discarded-initiative-cards {
      grid-area: initiative;
    }
    .dis-title {
      grid-area: dis-title;
      
    }
    .discarded-disruption-cards {
      grid-area: disruption;
    }
  }

