@import '../../mixins';

.landing-login-container {
  display: grid;
  position: absolute;
  height: 100%;
  width: 100%;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 3fr 1fr;
  row-gap: 1rem;
  column-gap: 1rem;
  button {
    color: whitesmoke;
    height: 100%;
    width: 100%;
    border-radius: 5px;
    border: 0;
  }
  input {
    text-align: center;
    border-radius: 5px;
    border: 0;
    opacity: 0.9;
    width: 100%;
    height: 100%;
  }
}

.landing-login-name {
  grid-area: 1/1/2/2
}

.landing-login-password {
  grid-area: 2/1/3/2
}

.landing-login-button {
  grid-area: 1/2/3/3
}

#user-landing-container {
  button {
    font-family: 'Cactus';
    font-size: 1.5rem;
    background-color: #f28e1c;
  }
  input {
    font-family: 'Cactus';
    font-size: 1.5rem;
  }
}