@import '../../variables';
@import '../../mixins';

.card-deck-display {
  position: absolute;
  @include grid-settings;
  top: 0;
  right: 0;
  height: 100vh;
  width: 12.5vw;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-areas:
  'initiative'
  'disruption'
  'discard';
  padding: 1rem;
}

.deck {
  background-repeat: no-repeat;
  background-size: contain;
  height: $card-deck-height;
  width: $card-deck-width;
}

.card-deck-display_initiative-deck {
  background-image: url('../../assets/decks/initiativeDeckBack.png');
  grid-area: initiative;
  border-radius: 10px;
}

.card-deck-display_disruption-deck {
  background-image: url('../../assets/decks/disruption-card-back.jpg');
  grid-area: disruption;
  border-radius: 10px;
}

.card-deck-display_discard-deck {
  background-image: linear-gradient(rgba(184, 184, 184, 0.7), rgba(44, 44, 44, 0.7)), url('../../assets/decks/discardDeckBack.png');
  grid-area: discard;
  border-radius: 10px;
  p{
    text-transform: uppercase;
    text-align: center;
    font-family: 'Cactus';
    font-weight: 7rem;
    color: rgba(29, 29, 29, 0.281);
  }
}

.card-deck-click {
  animation: 2s cubic-bezier(.55,.1,.82,.5) infinite pulse;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  
  25% {
    transform: scale(1.1);
  }
  
  50% {
    transform: scale(1);
  }
}