.lobby-container {
  height: 100vh;
  width: 100vw;
  overflow-y: scroll;
}

#lobby-language-labels {
  margin: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.lobby-language-container {
  select {
    font-family: sans-serif;
    font-size: 1.5rem;
    margin: 1rem;
    padding: 0.5rem;
    opacity: 0.8;
    // background-color: #1276bc;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
    border-radius: 0.5em;
    -moz-appearance: none;
    -webkit-appearance: none;
  }

  select:hover {
    border-color: #888;
  }
  select:focus {
    box-shadow: 0 0 1px 3px rgba(59, 153, 252, 0.7);
    box-shadow: 0 0 0 3px -moz-mac-focusring;
    color: #222;
    outline: none;
  }

  select::-ms-expand {
    display: none;
  }
}

.lobby-container:after {
  content: '';
  position: absolute;
  background-image: url('../../assets/lobbybg.png');
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.8;
  z-index: 1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: block;
}

.lobby-content-container {
  position: relative;
  z-index: 2;
  height: 60%;
  width: 100%;

  .lobby-header {
    margin-top: 2rem;
    margin-bottom: 1rem;
    font-family: 'Cactus';
    font-size: 3.5rem;
    text-align: center;
    text-decoration: underline;
  }

  .lobby-sub-header {
    text-align: center;
    font-style: italic;
  }
}

.lobby-content-repeat {
  display: grid;
  height: 100%;
  width: 100%;
  grid-template-columns: repeat(16, 1fr);
  grid-template-rows: 0.5fr repeat(5, 1fr) 0.5fr;
  grid-gap: 1rem;
}

.lobby-rooms-container {
  position: relative;
  height: 100%;
  width: 100%;
  grid-area: 2/2/7/8;
  justify-content: center;
  align-items: center;
}

.lobby-messages-container {
  height: 100%;
  width: 100%;
  grid-area: 2/9/7/16;
  background-image: linear-gradient(to bottom right, #0b3c5f91, #1275bc83);
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px;
  overflow: hidden;
}

