@import '../../mixins';
@import '../../colors';

.announcement-container {
  position: absolute;
  @include center;
  // min-height: 40vh;
  width: 20vw;
  background-color: rgba(255, 255, 240, 0.9);
  border-radius: 5px;
  color: black;
}

.announcement {
  text-align: center;
  margin: 1rem; 
}

.announcement-bubble {
  display: grid;
  grid-area: 6 / 10 / 14 / 30;
  z-index: 30;
  background-color: rgba(255, 255, 240, 0.9);
  border-radius: 5px;
  // justify-self: end;
  align-self: start;
}

.announcement-dialog-box-climmie {
  text-align: center;
  padding: 1rem;
}
