@import '../../mixins';
@import '../../variables';

.selected-cards-grey-out {
  // position: absolute;
  @include grey-out;
  @include full-screen;
}

.selected-cards {
  @include full-screen;
  @include grid-settings;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 15% 1fr 1fr 1fr 15%;
  z-index: 14;
  .selected-cards-display-item {
    justify-self: center;
    // margin: 0 10vw;
    &:hover {
      transform: scale(1.3);
    }
  }
  .role-card{
    align-self: center;
    justify-self: center;
    grid-area: 1/3/2/4;
  }
  .other-players-card1{
    grid-area: 2/2/3/3;
  }
  .other-players-card2{
    grid-area: 2/3/3/4;
    
  }
  .other-players-card3{
    grid-area: 2/4/3/5;

  }
}
