.room-container {
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 100%;
}

.room-container:after {
  content: '';
  position: absolute;
  overflow: hidden;
  border-radius: 10px;
  background-image: linear-gradient(to bottom right, #0b3c5f, #1276bc);
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.5;
  z-index: 1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: block;
}

.room-container-content {
  color: white;
  display: grid;
  position: absolute;
  height: 100%;
  width: 100%;
  grid-template-rows: repeat(8, 1fr);
  grid-template-columns: repeat(6, 1fr);
  z-index: 2;

  #room-title {
    font-size: 3rem;
    font-family: 'Cactus';
    text-align: center;
    grid-area: 1/2/2/6;
    justify-self: center;
    text-decoration: underline;
  }

  button {
    color: white;
    font-size: 1rem;
    font-family: 'Cactus';
    background-color: #f28e1c;
    height: 100%;
    width: 50%;
    grid-area: 4/3/6/5;
    border-radius: 100%;
    justify-self: center;
  }

  .room-players-container {
    display: grid;
    grid-template-rows: repeat(8, 1fr);
    grid-template-columns: repeat(6, 1fr);
    grid-area: 1/1/9/7;
    height: 100%;
    width: 100%;

    :nth-child(1) {
      grid-area: 6/3/8/5;

    }

    :nth-child(2) {
      grid-area: 4/1/6/3;
    }

    :nth-child(3) {
      grid-area: 4/5/6/7;
    }

    :nth-child(4) {
      grid-area: 2/3/4/5;
    }
  }
}
