@import '../../variables';
@import '../../mixins';
@import '../../colors';

.selected-card-display {
  @include top-left;
  @include grid-settings;
  grid-template-columns: 10vw 10vw 10vw 5vw 5vw 5vw 5vw 5vw 5vw 10vw 10vw 10vw 10vw;
  grid-template-rows: 10vh 10vh 10vh 10vh 10vh 10vh 10vh 10vh 10vh 10vh;
  transition: 0.2s ease-in-out;
}

.selected-card-greyed-out {
  @include grey-out;
  grid-area: 0/0/11/11;
}

.play-selected-card-btn,
.discard-selected-card-btn {
  // @include button;
  grid-area: 8/6/9/9;
  z-index: 11;
  font-family: 'Cactus';
  min-height: 3rem;
  font-size: large;
}

.selected-card {
  @include top-left;
  transform: scale(1.6);
  grid-area: 3/6/7/10;
  z-index: 11;
  &:hover {
    transform: scale(1.6);
  }
}

.discard-selected-card-btn {
  background-color: $day-of-adaptation-yellow;
  color: $white;
}