@import '../../mixins';
@import '../../colors';

.disruption-card-container {
  border-radius: 10px;
  background-image: url('../../assets/disruptioncards/disruptionCardEmpty.png');
  @include grid-settings;
  background-size: cover;
  position: relative;
  height: 35vh;
  width: 25vh;
  grid-template-rows: 3% 1.5fr 1.2fr 4fr 3%;
  grid-template-columns: 5% 1fr 5%;
  grid-template-areas:
    '. . .'
    '. title .'
    '. image .'
    '. text .'
    '. . .';
}

.disruption-card-name {
  background-color: $white;
  margin: 0.6rem;
  border: 1px solid $border-grey;
  border-radius: 5px;
  grid-area: title;
  text-align: center;
  p {
    margin: 0.2rem;
  }
  font-family: 'Cactus';
  font-size: larger;
}

.disruption-card-icon {
  margin: auto;
  grid-area: image;
  object-fit: cover;
  height: 100%;
  max-height: 100%;
  overflow: hidden;
}

.disruption-card-description {
  grid-area: text;
  font-size: small;
  background-color: $white;
  margin: 0.6rem;
  border: 1px solid $border-grey;
  border-radius: 5px;
  text-align: center;
  overflow: scroll;
  max-height: 100%;
  p{
   margin: 0.2rem;
   padding: 0 0.2rem;
  }
}

.crystal {
  position: absolute;
  left: 10%;
  opacity: 0.4;
}

.crystal-number {
  font-size: 8rem;
  border: 3px solid black;
  background-color: grey;
  border-radius: 10rem;
  padding-right: 3rem;
  padding-left: 3rem;
  padding-bottom: 2rem;
}
