@import '../../variables';
@import '../../colors';
@import '../../mixins';

.player-display {
  height: $player-display-height;
  width: $player-display-width;
  @include grid-settings;
  grid-template-rows: repeat(auto-fill, minmax(1fr, 1fr));
  padding: 1rem;
}

.table-name-and-score {
  font-family: 'Cactus';
  font-size: large;
  font-weight: 600;
  color: $white;
  margin: 2rem;
  span {
    font-family: 'Raleway';
    font-weight: 600;
  }
}

.player-display-frame {
  position: relative;
  width: $player-display-frame-size;
  height: $player-display-frame-size;
  border-radius: 100px;
}

.player-display-frame_img {
  position: absolute;
  max-width: 100%;
  max-height: 100%;
  border-radius: 100px;
}

.player-display-frame_stats {
  position: absolute;
  text-align: center;
  font-size: 1.3rem;
  top: 80%;
  background-color: #ffffff;
  border: 1px solid $border-grey;
  border-radius: 5px;
  width: 100%;
  h5 {
    font-family: 'Cactus';
    margin: 0.1rem;
  }
  p {
    margin: 0.1rem;
    font-size: 0.9rem;
    display: none;
  }
}

.user {
  transform: scale(1.2);
}

.current-player-highlight {
  box-shadow: 0px 0px 10px 10px $highlight-color;
  z-index: 2;
  p {
    display: inline;
  }
}

.player-role-Alex {
  border: 0.5rem solid lighten($color-alex, $player-display-color-lighten);
}
.player-role-Chris {
  border: 0.5rem solid lighten($color-chris, $player-display-color-lighten);
}
.player-role-Gaby {
  border: 0.5rem solid lighten($color-gaby, $player-display-color-lighten);
}
.player-role-Jamie {
  border: 0.5rem solid lighten($color-jamie, $player-display-color-lighten);
}
.player-role-Max {
  border: 0.5rem solid lighten($color-max, $player-display-color-lighten);
}
.player-role-Robin {
  border: 0.5rem solid lighten($color-robin, $player-display-color-lighten);
}
.player-role-Sam {
  border: 0.5rem solid lighten($color-sam, $player-display-color-lighten);
}

.player-display-icons {
  position: absolute;
  top: 90%;
  left: 0%;
  height: 10vh;
  width: 12.5vw;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  margin-left: 0;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.display-icon {
  font-size: 3rem;
  color: $white;
  transition: color 0.2s ease-in-out;
  &:hover {
    color: $day-of-adaptation-yellow;
  }
}

.icon-reference {
  font-size: 2.5rem;
}
