@import './colors';

@mixin grid-settings {
  display: grid;
  align-items: center;
  justify-content: center;
}

@mixin website-grid {
  height: 100%;
  width: 100%;
  display: grid;
  position: absolute;
}

@mixin background-image {
  background-size: contain;
  background-repeat: no-repeat;
}
@mixin top-left {
  position: absolute;
  top: 0;
  left: 0;
}
@mixin full-screen {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
}

@mixin center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@mixin grey-out {
  z-index: 10;
  background-color: rgb(48, 47, 47);
  opacity: 0.6;
  //TODO why ?
  height: 120vw;
  width: 100%;
  position: absolute;
}
