@import '../../colors';

.admin-container {
  height: 100vh;
  width: 100vw;
  overflow-y: scroll;
}

.admin-container:after {
  content: '';
  position: absolute;
  background-image: url('../../assets/lobbybg.png');
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.8;
  z-index: 1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: block;
}

.admin-content-container {
  position: absolute;
  z-index: 2;
  height: 100%;
  width: 100%;
  font-family: 'Cactus';
  // font-size: 2rem;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(8, 1fr);
  display: grid;

  button {
    color: whitesmoke;
    background-color: #f28e1c;

    // box-shadow: 10px 10px 8px #888888
  }
  h1 {
    margin: 1rem 0;
    font-size: 3rem;
    grid-area: 1/4/2/8;
    text-align: center;
  }
}

.choices-container {
  font-size: 2.3rem;
  grid-area: 3/4/6/8;
  justify-self: center;
  align-self: center;

  form {
    display: grid;
  }

  button {
    font-size: 2rem;
    padding: 1rem;
    margin:1.5rem;
    font-family: 'Cactus';
  }

  #button-sml {

  }

  select {
    font-family: sans-serif;
    font-size: 1.5rem;
    margin: 1rem;
    padding: 0.5rem;
    opacity: 0.8;
    // background-color: #1276bc;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
    border-radius: .5em;
    -moz-appearance: none;
    -webkit-appearance: none;
  }

  select:hover {
    border-color: #888;
  }
  select:focus {
    box-shadow: 0 0 1px 3px rgba(59, 153, 252, .7);
    box-shadow: 0 0 0 3px -moz-mac-focusring;
    color: #222;
    outline: none;
  }

  select::-ms-expand {
    display: none;
  }

  input {
    opacity: 0.8;
    font-family: sans-serif;
    font-size: 1.5rem;
    margin: 0 auto;
    padding: 0.5rem;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
    border-radius: .5em;
    width: 50%;
    text-align: center;
  }

  input[type=number] {
    -moz-appearance: textfield;
  }

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}

#admin-token-labels {
  margin: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.token-container {
  grid-area: 2 / 4 / 3 / 8;
  justify-self: center;
  align-self: center;
  font-size: xx-large;
}

.token {
  // grid-area: 2/3/3/9;
  font-size: 3rem;
}

#admin-token {
  text-shadow: -2px 0 black, 0 2px black, 2px 0 black, 0 -2px black;
  color: #f28e1c;
  font-weight: bold;
}

.admin-room-overview {
  display: grid;
  grid-template-columns: repeat(16, 1fr);
  grid-template-rows: repeat(12, 1fr);
  grid-gap: 1rem;
  height: 100%;
  width: 100%;
  // position: absolute;
  grid-area: 4/1/9/11;
}

.admin-rooms-container {
  height: 100%;
  width: 100%;
  grid-area: 3/3/11/8;
}

.admin-input-container {
  width: 100%;
  height: 100%;
  grid-area: 11/10/12/16;

  input {
    height: 2.5rem;
  }

}

.admin-room-buttons-text {
  grid-area: 1/3/2/5;
  justify-self: start;
  align-self: end;
  font-size: x-large;
}

.admin-room-buttons {
  grid-area: 2/3/3/6;
  button {
    font-family: 'Cactus';
    font-size: 1.2rem;
    padding: 0.5rem;
    margin:0.5rem;
  }
}

.start-game-button {
  font-family: 'Cactus';
  font-size: 1.5rem;
  grid-area: 3/5/4/7;
}

.admin-messages-container {
  height: 100%;
  width: 100%;
  grid-area: 3/10/11/15;
  background-image: linear-gradient(to bottom right, #0b3c5f91, #1275bc83);
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 10px;
  overflow-y: scroll;
  font-family: 'Raleway', sans-serif;
}

.chat-message-container {
  overflow-y: scroll;
}

.chat-msg-element {
  color: $white;
  margin-left: 0.5rem;
  background-color: #0b3c5fc2;
  margin: 2.5%;
  padding: 0.2rem;
  border-radius: 5px;
  .msg-meta {
    font-size: x-small;
    font-style: italic;
  }
}

.create-game-buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.admin-language-form {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  select {
    border: 0px;
    border-radius: 5px;
  }
}
