@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,400;0,500;0,600;0,700;0,800;0,900;1,300&display=swap');
@import './colors';

*{
  box-sizing: border-box;
  &:focus {
    outline: none;
  }
}

@font-face {
  font-family: 'Cactus';
  src: local('Cactus'), url(./fonts/cactus_regular-webfont.woff) format('woff');
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button {
  &,
  &:link,
  &:visited {
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    // padding: 1rem 4rem;
    border-radius: 5px;
    transition: all 0.2s;
    border: 1px solid $border-grey;
    cursor: pointer;
    opacity: 0.9;
    font-weight: 800;
    background-color: $white;
  }

  &:hover {
    transform: translateY(-3px);
    box-shadow: 0 1rem 2rem rgba(black, 0.2);
  }

  &:active,
  &:focus {
    outline: none;
    transform: translateY(-1px);
    box-shadow: 0 0.5rem 1rem rgba(black, 0.2);
  }
}