@import '../../variables';
@import '../../colors';
@import '../../mixins';

.role-card-container {
  background-image: url('../../assets/roles/roles-card-blank.jpg');
  @include background-image;
  position: relative;
  width: calc(#{$role-card-height}* 0.72);
  height: $role-card-height;
  max-width: 100%;
  @include grid-settings;
  justify-self: center;
  grid-template-rows: 5% 3fr 1fr 2fr 0.5fr 3fr 3%;
  grid-template-columns: 5% 1fr 5%;
  text-align: center;
  grid-template-areas:
    '. . .'
    '. image .'
    '. image .'
    '. text .'
    '. powerLabel.'
    '. power .'
    '. . .';
}

.role-name {
  padding-left: 0.3rem;
  font-size: 1.3rem;
  grid-area: image;
  text-align: center;
  align-self: start;
  color: $white;
  justify-self: start;
  z-index: 2;
  h3 {
    margin: 0.2rem;
  }
}

.role-icon {
  margin: auto;
  grid-area: image;
  height: 100%;
  max-height: 100%;
  background-size: contain;
}

.role-text {
  font-size: small;
  background-color: white;
  margin: 0.6rem;
  border: 1px solid $border-grey;
  border-radius: 5px;
  text-align: center;
  p {
    margin: 0.2rem;
    padding: 0 0.2rem;
  }
}

h5 {
  margin: 0.2rem;
}

.role-description {
  grid-area: text;
}

.power-label {
  grid-area: powerLabel;
}

.role-power {
  margin-top: 0;
  align-self: start;
  grid-area: power;
}
