@import '../../colors';
@import '../../variables';
@import '../../mixins';

.test-text {
  font-size: 2.5vh;
  font-weight: 700;
  color: $day-of-adaptation-dark-blue;
}

.game {
  position: relative;
  height: 100vh;
  overflow: hidden;
  width: 100vw;
  background-image: url('../../assets/wood.jpg');
}

.board {
  position: absolute;
  @include center;
  height: $board-height;
  width: $board-width;
  margin: auto;
}

.elements-grid {
  position: absolute;
  display: grid;
  grid-template-columns: 4fr 6fr 2fr 3fr 8fr 1fr 4fr 1fr 3fr 1fr 3fr 1fr 1fr 2fr 1fr 2fr 2fr 1fr 1fr 3fr 2fr 1fr 1fr 1fr 2fr 2fr 3fr 2fr 1fr 2fr 5fr 1fr 8fr 1fr 11fr 4fr;
  grid-template-rows: 2fr 2fr 1fr 5fr 6fr 3fr 5fr 1fr 2fr 2fr 1fr 1fr 1fr 1fr 2fr 1fr 1fr 4fr 1fr 1fr 3fr 1fr 2fr 2fr 3fr 1fr 2fr 4fr 2fr 2fr 2fr 1fr 3fr;
  width: 100%;
  height: 100%;
}

.board-background {
  position: absolute;
  background-image: url('../../assets/backgroundWithPathways.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 100%;
  z-index: -4;
}

.board-element {
  background-size: cover;
  background-position: center;
  position: relative;
}

