/* MAIN FUNCTIONAL COMPONENTS */

.funding {
  position: absolute;
  @include center;
  color: $white;
  font-size: 1.8rem;
  font-family: 'Cactus';
  text-shadow: -1px 0 $day-of-adaptation-dark-blue, 0 1px $day-of-adaptation-dark-blue,
    1px 0 $day-of-adaptation-dark-blue, 0 -1px $day-of-adaptation-dark-blue;
}

.earning-level {
  background-image: url('../../assets/gauge.png');
  grid-area: 6/29/11/37;
  @include grid-settings;
  grid-template-columns: 7% 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 6%;
  grid-template-rows: 10% 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 25%;
}

.climmie-earn {
  background-image: url('../../assets/icons/e-climmie.png');
  @include background-image;
  height: 100%;
  width: 100%;
  transform: scale(1.3);
  z-index: 2;
}

.climmie-earn1 {
  grid-area: 7/13/9/14;
  transform: translate(10%, -20%) scale(1.3);
}
.climmie-earn2 {
  grid-area: 8/12/10/13;
  transform: translate(20%) scale(1.3);
}
.climmie-earn3 {
  grid-area: 5/12/7/13;
  transform: translate(20%, 10%) scale(1.3);
}
.climmie-earn4 {
  grid-area: 7/11/9/12;
  transform: translate(30%, -10%) scale(1.3);
}
.climmie-earn5 {
  grid-area: 4/11/6/12;
  transform: translate(20%) scale(1.3);
}
.climmie-earn6 {
  grid-area: 6/10/8/11;
  transform: translate(30%, -10%) scale(1.3);
}
.climmie-earn7 {
