  transform: translate(10%, 10%) scale(1.3);
}

.money-light {
  height: 70%;
  width: 70%;
  background-color: $highlight-color;
  border-radius: 50%;
  grid-area: 27/-36/30/-34;
  z-index: 0;
  box-shadow: 0 0 1rem 1rem $highlight-color;
  transform: translate(20%, 20%);
}
.money {
  background-image: url('../../assets/coin.png');
  grid-area: 27/-36/30/-34;
  z-index: 1;
}

.click-money {
  background-color: transparent;
  z-index: 15;
  background-image: none;
}

.money-mover {
  animation: 1.5s ease-in-out infinite pulse;
}

.victory-track {
  background-image: url('../../assets/victoryTrack.png');
  grid-area: 27/29/32/36;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 22%;
  grid-template-rows: 13% 1fr 0.4fr 1fr;
  grid-template-areas:
    '. . . . . .'
    '. shield2 . shield4 . .'
    '. . . . . .'
    'shield1 . shield3 . shield5 .';
}

.tut {
  z-index: 12;
}

.tut-hand {
  transform: translate(-50%, -15vh) scale(1.2);
  .card {
    height: 26vh;
    width: 13vw;
    transition: 0.1s ease-in-out;
    transform: rotate(4deg);
  }
}
// Inside victory track

.created-element{
  img {
    position: absolute;
    height: 40%;
    max-height: 40%;
    background-color: #ffd794;
    border: 1px solid $day-of-adaptation-dark-blue;
    border-radius: 5px;
  }
}

.shield-icons {
  img {
    position: absolute;
    height: 40%;
    max-height: 40%;
    background-color: #ffd794;
    border: 1px solid $day-of-adaptation-dark-blue;
    border-radius: 5px;
  }
}

.shield-icon1 {
  grid-area: shield1;
}
.shield-icon2 {
  grid-area: shield2;
}
.shield-icon3 {
  grid-area: shield3;
}
.shield-icon4 {
  grid-area: shield4;
}
.shield-icon5 {
  grid-area: shield5;
}

.core {
  background-image: url('../../assets/coreEmpty.png');
  display: grid;
  grid-template-columns: 2.2fr 1.8fr 1fr 0.5fr 0.5fr 1fr 1.8fr 2fr;
  grid-template-rows: 2fr 1.7fr 0.5fr 1.7fr 1fr;
  @include background-image;
}

.core-hover-on {
  &:hover {
    transform: scale(1.1);
    cursor: pointer;
  }
}

.core-highlight {
  align-self: center;
  justify-self: center;
  height: 60%;
  width: 40%;
  background-color: $highlight-color;
  border-radius: 50%;
  z-index: 0;
  box-shadow: 0 0 1rem 1rem $highlight-color;
  transform: translateY(20%);
}

.climmie-on-core {
  background-image: url('../../assets/climmieOnCore.png');
  background-size: contain;
  background-repeat: no-repeat;
  height: 110%;
  transform: translate(8%, -10%);
}
.climmie1 {
  grid-area: 2/3/3/7;
}
.climmie2 {
  grid-area: 4/2/5/4;
  transform: translate(20%, -15%);
}
.climmie3 {
  grid-area: 4/6/5/8;
  transform: translate(-8%, -15%);
}

/* MINIONS */

.zillian {
  background-image: url('../../assets/zil.png');
  grid-area: 27/6/31/10;
  transform: translate(-100%);
}

.carbion {
  background-image: url('../../assets/carb.png');
  grid-area: 5/2/8/5;
}

.board-dialog-box-pointer-carbion {
  height: 2rem;
  width: 2rem;
  background: $white;
  margin: 0 auto;
  transform: rotate(45deg);
  border-radius: 0 0 12px 0;
  margin-top: -1vh;
  position: relative;
  transition: all 1 ease-out;
  left: -3vw;
  top: -2vh;
  z-index: -1;
}

.board-dialog-box-carbion {
  background-color: $white;
  grid-area: d;
  min-height: 7vh;
  width: 17vw;
  background: $white;
  display: block;
  margin: 0 auto;
  border-radius: 20px;
  text-align: center;
  font-size: medium;
  .board-dialog-box-carbion-text {
    padding: 0.1rem;
    margin: 0.2rem;
    span {
      color: $day-of-adaptation-yellow;
      font-weight: 800;
    }
  }
}

.board-message-container-carbion {
  top: 0vh;
  left: 4vw;
  position: absolute;
}

.climmie {
  background-image: url('../../assets/climmie.png');
  grid-area: 4/30/6/32;
}

/* SHIELDS */
.greyed-out-shield {
  @include grey-out;
}

.shield {
  display: grid;
  grid-template-columns: 17% 1fr 0.5fr 1fr 0.4fr 1fr 0.5fr 1fr 26%;
  grid-template-rows: 25% 15% 8% 1fr 1.4fr 1fr 0.9fr 10%;
  @include background-image /* z-index: 4; */;
}

.shield-highlight {
  z-index: 10;
  transition: 0.2s ease-in-out;
}

.shield-highlight:hover {
  transform: scale(1.1);
}

// Inside shield

.zil-on-shield {
  background-image: url('../../assets/icons/e-zillian.png');
  @include background-image;
  height: 110%;
  width: 110%;
  align-self: center;
  justify-self: center;
  z-index: 4;
}

