  grid-area: 5/2/6/5;
}
.zil2 {
  grid-area: 5/6/6/9;
}
.zil3 {
  grid-area: 6/4/8/7;
}
.zil4 {
  grid-area: 2/2/3/5;
}
.zil5 {
  grid-area: 2/6/3/9;
}

.housing {
  grid-area: 4/13/7/18;
}

.work {
  grid-area: 4/24/7/28;
}

.transportation {
