}

.shield-animation-div {
}

/* SPOTS */

.clickable-hotspot.spot,
.clickable-hotspot.start,
.send-climmie-hotspot,
.move-teammate-to-hotspot {
  border-radius: 50px;
  box-shadow: inset 0px 0px 10px 8px $highlight-color;
  transition: 0.2s ease-out;
  &:hover {
    transform: scale(1.1);
    cursor: pointer;
  }
}

.send-climmie-hotspot {
  box-shadow: inset 0px 0px 10px 8px $target-color;
}

.move-teammate-to-hotspot {
  box-shadow: inset 0px 0px 10px 8px green;
}

.spot {
  background-image: $spots-url;
}

.start {
  background-image: url('../../assets/startSpot.png');
}

.climmie-on-hotspot {
