/* seats */

.seat0 {
  transform: translate(0);
}
.seat1 {
  transform: translateX(50%);
}
.seat2 {
  transform: translateY(50%);
}
.seat3 {
  transform: translate(50%, 50%);
}

.big-pawn {
  animation: 3s cubic-bezier(1,-0.07,1,1.07) infinite shake;
}

.btn-remove-climmie,
.end-turn-btn {
  grid-area: 4/33/6/36;
  font-family: 'Cactus';
  font-size: xx-large;
}

.end-turn-btn {
  background-color: $day-of-adaptation-yellow;
  color: $white;
  opacity: 0.97;
  border: 0px;
  box-shadow: 0 0.5rem 1rem rgba(black, 0.2);
  transition: 0.2s ease-out;
  &:hover {
    background-color: darken($day-of-adaptation-yellow, 5%);
  }
}

.player-pawn {
  &.spot0 {
    @include center;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  25% {
    transform: scale(1.2);
  }

  50% {
    transform: scale(1);
  }
}

@keyframes shake {
  0% {
    transform: rotate(0deg) scale(1.3) translateY(-50%);
  }
  90% {
    transform: rotate(7deg) scale(1.3) translateY(-50%);
  }
  100% {
    transform: rotate(-7deg) scale(1.3) translateY(-50%);
  }
}
