@import '../../mixins';
@import '../../colors';

.initiative-card-container {
  border-radius: 10px;
  background-image: url('../../assets/initiativecards/initiativeCardEmpty.png');
  @include grid-settings;
  background-size: cover;
  position: relative;
  height: 35vh;
  width: 25vh;
  grid-template-rows: 3% 1.5fr 1.8fr 4fr 3%;
  grid-template-columns: 5% 1fr 5%;
  grid-template-areas:
    '. . .'
    '. title .'
    '. image .'
    '. text .'
    '. . .';
}

.initiative-card-name {
  background-color: $white;
  margin: 0.6rem;
  border: 1px solid $border-grey;
  border-radius: 5px;
  grid-area: title;
  text-align: center;
  font-family: 'Cactus';
  font-size: larger;
  p {
    margin: 0.2rem;
  }
}

.initiative-card-icon {
  margin: auto;
  grid-area: image;
  // width: '50px';
  // height: '50px';
  overflow: hidden;
  object-fit: cover;
  height: 100%;
  max-height: 100%;
}

// style={{ width: '50px', height: '50px' }}
.initiative-card-description {
  grid-area: text;
  font-size: small;
  background-color: $white;
  margin: 0.6rem;
  border: 1px solid $border-grey;
  border-radius: 5px;
  text-align: center;
  overflow: scroll;
  max-height: 100%;
  p {
    margin: 0.2rem;
    padding: 0 0.2rem;
  }
}

.special-card-label {
  height: 100%;
  width: 100%;
  font-size: 5rem;
  color: #ef5623;
  position: absolute;
  top:-30%;
  left: -30%;
  opacity: 0.9;
  .fa-layers{
    stroke: $day-of-adaptation-dark-blue;
    .fa-layers-text{
      color: $white;
      font-family: 'Cactus';
      font-size: 1.5rem;
      margin-left: 0.4rem;
    }
  }
}

.small{
  transform: scale(0.5);
  opacity: 1;
}
