@import '../../mixins';
@import '../../colors';
@import '../../variables';

.dialog-container,
.tga-container,
.tga-waiting-container {
  position: absolute;
  @include center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  // min-height: 40vh;
  min-width: 20vw;
  background-color: rgba(255, 255, 240, 0.9);
  border-radius: 5px;
  color: black;
}

.dialog-text {
  text-align: center;
  color: black;
  margin: 1rem;
  // font-weight: lighter;
  // font-size: large;
}

// .dialog-button {
//   @include button;
// }

.dialog-buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.dialog-button {
  height: 3rem;
  width: 8rem;
  font-family: 'Cactus';
  font-size: larger;
  background-color: $day-of-adaptation-yellow;
  border: 0px;
  border-radius: 5px;
  margin: 1rem;
  transition: .2s ease-in-out;
  &:hover {
    box-shadow: 0 0 1px 2px #3333;
    transform: scale(1.1);
  }
}

.amount-text {
  color: black;
  margin-bottom: 1rem;
  margin-top: 1rem;
  text-align: center;
}

.choose-player-container {
  position: absolute;
  @include center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  min-width: 20vw;
  background-color: rgba(128, 127, 120, 0.8);
  border-radius: 0.5rem;
  color: black;
}

.player-picker-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.player-picker {
  margin: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; 
}

.player-display-frame {
  transition: transform .2s ease-in-out;
  &:hover {
    box-shadow: 0 0 1px 3px rgba(245, 245, 245, 0.5);
    transform: scale(1.1);
  }
}

.tga-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tga-waiting-container {
  padding: 3rem;
  color: black;
}

.tga-slider {
  width: 50%;
}